.review-card {
	background-color: #290076;
	border: 1px solid #ccc;
	padding: 1.2rem;
	margin: 1rem auto;
	border-radius: 10px;
	max-width: 500px;
	box-shadow: 0 4px 100px rgba(0, 0, 0, 0.08);
	font-family: sans-serif;
}

	.review-card h2 { 
		/*Name*/
		margin-top: 0;
		font-size: 1.5rem;
		/*color: #333;*/
	}

	.review-card h3 { 
		/*Brand*/
		margin-top: 0;
		font-size: 1.3rem;
		/*color: #333;*/
	}

	.review-card p {
		/*Everything else*/
		margin: 0.5rem 0;
		line-height: 1.4;
	}

	.review-scores {
		display:grid;
		grid-template-columns: repeat(4, 1fr) ;
		grid-template-rows: auto auto;
		text-align: center;
		margin-top: 100px;
		width: 90%;
		margin:0 auto; /*centers the grid*/
	}

.score {
	font-size: 2.5rem;
	font-weight: bold;
	/*color: #222;*/
}

.label {
	font-size: 1rem;
	/*color: #666;
	*/
}